import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CrearPermisoPage } from './crear-permiso.page';

const routes: Routes = [
  {
    path: '',
    component: CrearPermisoPage,
  },
  {
    path: 'inicio-permiso',
    loadChildren: () => import('src/app/pages/SST/inicio-permiso/inicio-permiso.module').then(m => m.InicioPermisoPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CrearPermisoPageRoutingModule {}
